
.Input{
 
    width: 1000px;
    display: flex;
    height: 60px;
    background: #fff;
    border-radius: 10px;
    overflow: hidden;

    input{
        flex: 8;
       height: 100%;
        padding: 0px 20px;
        outline: none;
        border: none;
    }
    button{
        flex: 1;
        height: 100%;
        background: #FFA101;
        border: none;
        outline: none;
        color: #fff;
        font-size: 22px;
        font-weight: 700;
        cursor: pointer;
       
    }

}

@media only screen and (max-width: 768px) {
  .Input{
    width: 80%;
  }
}